import React, { useState } from "react";
import styled from "styled-components";
import Input2 from "../components/Input2";
import Button from "../components/Button";
import logo from "../assets/logo.png";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { signup } from "../_actions/actions";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90vh;
`;

const Items = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 650px;
`;

const TextBox = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  //justify-content: flex-start;
  width: 98%;
  color: #064538;
`;

const Checkbox = styled.input`
  margin-right: 1rem;
  width: 2rem;
  height: 2rem;
  border: 1px solid #cccccc;
  border-radius: 10px;
  background-color: #56be9c;
  cursor: pointer;
`;

const Signup = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const [user, setUser] = useState({
    realname: "",
    email: "",
    pw: "",
    pw2: "",
  });

  const onInputChange = (e) => {
    const { name, value } = e.target;
    if (name !== "useragree") setUser({ ...user, [name]: value });
    else setUser({ ...user, useragree: e.target.checked ? "Y" : "N" });
  };

  const formSubmit = async (evt) => {
    evt.preventDefault();
    if (user.useragree !== "Y") alert("    .");
    else if (user.email.slice(-11) !== "@ewhain.net") {
      console.log(user.email.slice(-11));
      alert("@ewhain.net  ");
    } else if (user.pw !== user.pw2)
      alert("    .");
    else {
      const submitUser = {
        realname: user.realname,
        email: user.email,
        pw: user.pw,
      };
      dispatch(signup(submitUser)).then((response) => {
        console.log(response);
        if (response.type) {
          alert("     ");
          history.push("/login");
        } else {
          alert(" ");
        }
      });
    }
  };

  return (
    <Wrapper>
      <form onSubmit={formSubmit}>
        <Items>
          <img
            alt=""
            src={logo}
            style={{ marginBottom: "1rem", cursor: "pointer" }}
            onClick={() => history.push("/")}
          ></img>
          <Input2
            name="realname"
            placeholder=""
            width="29rem"
            onChange={onInputChange}
          ></Input2>
          <Input2
            name="email"
            placeholder="ewhain  "
            width="29rem"
            onChange={onInputChange}
          ></Input2>
          <Input2
            name="pw"
            type="password"
            placeholder="(, ,   8~16)"
            width="29rem"
            onChange={onInputChange}
          ></Input2>
          <Input2
            name="pw2"
            type="password"
            placeholder=" "
            width="29rem"
            onChange={onInputChange}
          ></Input2>
          <TextBox>
            <Checkbox
              name="useragree"
              type="checkbox"
              id="agree"
              onChange={onInputChange}
            ></Checkbox>
            <label for="agree">
              <div>    .</div>
            </label>
          </TextBox>
          <Button
            type="submit"
            name=""
            width="29rem"
            color="#56BE9C"
          ></Button>
        </Items>
      </form>
    </Wrapper>
  );
};

export default Signup;
