import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import Header from "../components/Header";
import Title from "../components/Title";
import Input from "../components/Input";
import Search from "../assets/search.png";
import Image from "../components/Image";
import Warn from "../components/Warn";
import {
  getStudydetail,
  getMember,
  warnMember,
  leaderMakeChat,
} from "../_actions/actions";
import { useDispatch } from "react-redux";
import Chat from "../components/Chat";

const Fix = styled.div`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
  margin-top: 3%;
`;
const Row = styled.div`
  width: 80%;
  height: 90px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: ${(props) => props.border || "1px solid #56be9c"};
`;

const MemberList = ({ match }) => {
  const { study_id } = match.params;
  console.log(study_id);
  const dispatch = useDispatch();
  const [study, setStudy] = useState("");
  const [members, setMembers] = useState([]);
  const [isLeader, setIsLeader] = useState(false);
  const room = useRef();
  const member_id = useRef();

  useEffect(() => {
    dispatch(getStudydetail(study_id)).then((response) => {
      if (response.payload) {
        console.log(response.payload);
        setStudy(response.payload.title);
        if (
          response.payload.leader.toString() ===
          window.localStorage.getItem("id")
        )
          setIsLeader(true);
      } else {
        console.log("    ");
      }
    });
    dispatch(getMember(study_id)).then((response) => {
      if (response.payload) {
        console.log("    ");
        console.log(response.payload);
        setMembers(response.payload);
      } else {
        console.log("    ");
      }
    });
  }, []);

  const warn = (user_idx) => {
    const data = {
      study_idx: study_id,
      user_idx: user_idx,
    };
    console.log(data);
    dispatch(warnMember(data)).then((response) => {
      if (response.type) {
        console.log(" ");
        dispatch(getMember(study_id)).then((response) => {
          if (response.payload) {
            console.log("    ");
            console.log(response.payload);
            setMembers(response.payload);
          } else {
            console.log("    ");
          }
        });
        openModal();
      } else {
        console.log("   ");
        console.log(response);
      }
    });
  };

  //
  // useState  open . (open true   )
  const [modalOpen, setModalOpen] = useState(false);

  const openModal = () => {
    setModalOpen(true);
  };
  const closeModal = () => {
    setModalOpen(false);
  };

  const [chatModalOpen, setChatModalOpen] = useState(false);
  const openChatModal = () => {
    setChatModalOpen(true);
  };

  const closeChatModal = () => {
    setChatModalOpen(false);
  };

  const leaderChat = (member_idx) => {
    member_id.current = member_idx;
    const data = {
      study_idx: study_id,
      leader_idx: window.localStorage.getItem("id"),
      member_idx: member_idx,
    };
    dispatch(leaderMakeChat(data)).then((response) => {
      if (response.payload) {
        room.current = response.payload.room_idx;
        openChatModal();
      } else {
        alert("  ");
      }
    });
  };
  return (
    <>
      {chatModalOpen ? (
        <Chat
          open={chatModalOpen}
          close={closeChatModal}
          header=""
          other_user={member_id.current}
          room_idx={room.current}
        ></Chat>
      ) : (
        <></>
      )}
      <Header page="3" />
      <Fix>
        <Row border="none" style={{ marginTop: "1%" }}>
          <Title size="32">{study}</Title>
        </Row>
        <div></div>
        <Row border="none">
          <div
            style={{
              width: "70%",
              marginLeft: "30px",
              display: "flex",
              flexDirection: "row",
              justifyContent: "space-between",
              marginTop: "2%",
            }}
          >
            <Title size="18"> </Title>
            <Title size="18"></Title>
            <Title size="18"> </Title>
          </div>
        </Row>
        <div
          style={{
            width: "80%",
            border: "3px solid #56BE9C",
            background: "#56BE9C",
          }}
        ></div>
        {members?.map((member, i) => (
          <Row>
            <div
              style={{
                width: "70%",
                display: "flex",
                flexDirection: "row",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  alignItems: "center",
                }}
              >
                <Image
                  alt="profile"
                  src={member.user_image}
                  // onerror={Default}
                  // src={require("../assets/circle.png").default}
                  width="80"
                  height="80"
                />
                &emsp;
                <Title weight="400" size="20">
                  {member.user_name}
                </Title>
              </div>
              <Title weight="400" size="20">
                {member.user_manner}C
              </Title>
              <Title weight="400" size="20">
                {member.user_warning}
              </Title>
            </div>
            <div
              style={{
                display: "flex",
                flexDirection: "row",
                justifyContent: "flex-end",
                alignItems: "center",
              }}
            >
              {isLeader &&
              String(member.user_idx) !== window.localStorage.getItem("id") ? (
                <button
                  style={{
                    border: "1px solid #56BE9C",
                    borderRadius: "10px",
                    background: "white",
                    width: "105px",
                    height: "45px",
                    fontSize: "20px",
                    color: "#56BE9C",
                    fontFamily: "NotoSans",
                    cursor: "pointer",
                    marginRight: "30px",
                  }}
                  onClick={() => leaderChat(member.user_idx)}
                >
                  
                </button>
              ) : (
                <></>
              )}
              {isLeader &&
              String(member.user_idx) !== window.localStorage.getItem("id") ? (
                <button
                  style={{
                    border: "1px solid #C70000",
                    borderRadius: "10px",
                    background: "white",
                    width: "105px",
                    height: "45px",
                    fontSize: "20px",
                    color: "#C70000",
                    fontFamily: "NotoSans",
                    cursor: "pointer",
                    marginRight: "30px",
                  }}
                  // onClick={openModal}
                  onClick={() => warn(member.user_idx)}
                >
                  
                </button>
              ) : (
                <></>
              )}
            </div>

            <React.Fragment>
              {/* header   . */}
              <Warn open={modalOpen} close={closeModal} header=""></Warn>
            </React.Fragment>
          </Row>
        ))}
      </Fix>
    </>
  );
};

export default MemberList;
